<template>
    <div>
        <headers :title="title"></headers>
        <div class="add-write">
            <ul>
                <li>
                    <font class="fl">收件人姓名</font><span class="fl col-0">赵丽颖</span>
                </li>
                 <li>
                    <font class="fl">联系方式</font><span class="fl col-0">138888888888</span>
                </li>
                 <li>
                    <font class="fl">所在地区</font><span class="fl col-1" style="color: rgb(129,131,137);">请选择地区</span><i class="fr"></i>
                </li>
                 <li>
                    <font class="fl">详细地址</font><input class="fl col-1" placeholder="请输入详细地址">
                </li>
            </ul>
        </div>
        <footers :footerData="footerData"></footers>
    </div>
</template>

<style lang="less" scoped>
    .add-write{
        font-family: "PingFang-SC-Medium";
        margin-top: 115px;
        background: #fff;
        border-top: 1px solid #D4D4D6;
        width: 750px;
        ul{
            padding-left: 32px;
            li{         
                font-size: 28px;
                color: #000;   
                height: 98px;     
                border-bottom: 1px solid #D4D4D6;
                font{
                    font-family: "PingFang-SC-Medium";
                    color: #000000; 
                    width: 212px;
                    line-height: 98px;
                }
                span.col-0{
                    color: #000;  
                    line-height: 98px
                }
                .col-1{
                    line-height: 98px
                }
                .col-1::-webkit-input-placeholder{
                    color: rgb(129,131,137);  
                    
                }
                i{
                    width: 16px;
                    height: 98px;
                    background: url("../../assets/imgs/add-go.png") no-repeat left center;
                    background-size: 100%;
                    margin-right: 33px;
                }
            }
        }
       
    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/editHeader.vue'
import footers from '../../components/editfooter.vue'
export default {
  components:{
      headers,
      footers
  },
  data(){
      return{
          title: "地址填写",
          footerData: "保存"
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(247,246,247)";
  }
}
</script>





